import React, { Component } from 'react';
// import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import AppBar from 'material-ui/AppBar'
import {List , ListItem} from 'material-ui/List'
import RaisedButton from 'material-ui/RaisedButton'

export class Cornfirm extends Component {
    continue = e => {
        // process your form send data to api or backend
        e.preventDefault();
        this.props.nextStep();
    }

    back = e => {
        e.preventDefault();
        this.props.prevStep();
    }
    render() {
        const { values: {firstName, lastName, email, occupation, city, bio} } = this.props;


        return (
            <MuiThemeProvider>
                <React.Fragment>
                    <AppBar title="Confirm" />
                    <center>
                    <List>
                        <ListItem 
                        primaryText="First Name"
                        secondaryText ={firstName}
                        />

<ListItem 
                        primaryText="Last Name"
                        secondaryText ={lastName}
                        />
                        <ListItem 
                        primaryText="email"
                        secondaryText ={email}
                        />
                        <ListItem 
                        primaryText="occupation"
                        secondaryText ={occupation}
                        />
                        <ListItem 
                        primaryText="city"
                        secondaryText ={city}
                        />

<ListItem 
                        primaryText="bio"
                        secondaryText ={bio}
                        />
                    </List>

                        <RaisedButton
                            label="Back"
                            primary={false}
                            style={styles.button}
                            onClick={this.back}
                        />
 <RaisedButton
                            label="Continue"
                            primary={true}
                            style={styles.button}
                            onClick={this.continue}
                        />
                    </center>
                </React.Fragment>
            </MuiThemeProvider>
        )
    }

}

export default Cornfirm;
const styles = {
    button: {
        margin: 50
    }
}